.friendList {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 50px;
    list-style: none;
    gap: 15px;
    padding: 0;
}

.friendList .item {
    display: flex;
    gap: 20px;
    border-radius: 4px 4px 4px 4px;
    box-shadow: 0px 1px 6px rgba(46, 47, 66, 0.08), 0px 1px 1px rgba(46, 47, 66, 0.16), 0px 2px 1px rgba(46, 47, 66, 0.08);
    width: 350px;
    padding: 25px 20px;
    background: white;
}

.item img {
    border-radius: 4px 4px 4px 4px;
    box-shadow: 2px 2px rgba(46, 47, 66, 0.08),
        0px 1px 1px rgb(33 34 65 / 45%),
        0px 1px 1px rgb(154 156 182 / 24%);
    padding: 5px;
}

.name {
    font-size: 25px;
    margin: auto 0;
}

.online {
    background: #61c36d;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    margin: auto 0;
}

.offline {
    background: #e63e3e;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    margin: auto 0;
}